	this.getProblemNodeFromSelection = function(type) {
		var selection = this.getSelection(),
			isBackwards = selection.isBackwards(),
			range = selection.rangeCount() ? selection.getRangeAt(0) : null,
			collapsed = range ? range.collapsed() : true,
			selectionNode = selection.anchorNode(),
			rangeText,
			rangeNodes,
			element,
			problemWord,
			problemStartsFromRange,
			rangeStartsFromProblem,
			problemNode,
			problemNodeName,
			problemAttrName,
			problemClassName;

		switch (type) {
			case 'spelling':
				problemNodeName = this.getOption('misspelled_word_node');
				problemAttrName = this.getOption('data_attribute_name');
				problemClassName = this.getOption("misspelled_word_class");
				break;
			case 'grammar':
				problemNodeName = this.getOption('problem_grammar_node');
				problemAttrName = this.getOption('problem_grammar_data_attribute');
				problemClassName = this.getOption("problem_grammar_class");
				break;
		}

		// If selection node is node with some CORE UI problem or one of the parents of this node is node with problem
		if ( this.Utils.isEqualNode(selectionNode, problemAttrName) ) {
			problemNode = selectionNode;
		} else {
			problemNode = this.UILib.findNodeParents(selectionNode, problemNodeName, problemAttrName)[0];
		}

		// Sometimes browser create start of the selection in wrong place.
		// For example:
 		// Actual: ^<span class="spelling-problem" data-spellingParentsNode="mispelled" data-spelling-lang="en_US">mispelled</span>^
		// Expected: <span class="spelling-problem" data-spellingParentsNode="mispelled" data-spelling-lang="en_US">^mispelled^</span>
		if (!problemNode && !collapsed) {
			rangeText = this.removeSpecialCharacters( range.toString() );
			rangeText = isBackwards ? rangeText.split('').reverse().join('') : rangeText;

			rangeNodes = range.getNodes();
			rangeNodes = isBackwards ? rangeNodes.reverse() : rangeNodes;

 			for (var i = 0, len = rangeNodes.length; i < len; i++) {
 				element = rangeNodes[i];

 				// We need first node with CORE UI problem in nodes from the range
				if ( element.nodeType === 1 && this.UILib.hasClass(element, problemClassName) ) {
					problemWord = this.UILib.attr(element, problemAttrName);
					problemWord = isBackwards ? problemWord.split('').reverse().join('') : problemWord;

					problemStartsFromRange = (problemWord.length >= rangeText.length && problemWord.indexOf(rangeText) === 0);
					rangeStartsFromProblem = (problemWord.length < rangeText.length && rangeText.indexOf(problemWord) === 0);

					// If misspelled word contains range word in the start:
					// ^<span class="spelling-problem" data-spellingParentsNode="mispelled" data-spelling-lang="en_US">mispell^ed</span>
					// or range string contains misspelled word in the start:
					// ^<span class="spelling-problem" data-spellingParentsNode="mispelled" data-spelling-lang="en_US">mispelled</span> test^
					// then we can return this node like misspelled node
					if (problemStartsFromRange || rangeStartsFromProblem) {
 						problemNode = element;
					}

 					break;
				}
			}
		}

		return problemNode;
	};

